<template>
  <div class="systemUser">
    <div>
      <div class="bg">
        <div>
          <el-button class="input" @click="openDialog">添加用户</el-button>
        </div>
        <DxDataGrid :data-source="datalist" :allow-column-resizing="true" :show-column-lines="false"
          :auto-expand-all="false" :show-row-lines="true" :column-auto-width="true" :focused-row-enabled="true"
          :auto-navigate-to-focused-row="true" width="98%" key-expr="key" class="main_table"
          style="height: calc(335px + 42vh); width: 98%">
          <DxColumn data-field="id" alignment="center" caption="ID" />
          <DxColumn data-field="name" alignment="center" caption="用户名称" />
          <DxColumn data-field="account" alignment="center" caption="用户账号" />
          <DxColumn data-field="memo" alignment="center" caption="备注" />
          <DxColumn data-field="createtime" alignment="center" caption="创建日期" />
          <DxColumn data-field="rolename" alignment="center" caption="用户角色" />
          <DxColumn :width="180" caption="操作" alignment="center" cell-template="caozuo" />
          <template #caozuo="{ data: rowInfo }">
            <div class="caozuo_btn">
              <span @click="updatalist(rowInfo)" class="el-icon-edit caozuo_edit" title="修改"></span>
              <span @click="deletistinfo(rowInfo)" class="el-icon-delete caozuo_edit" title="删除"></span>
            </div>
          </template>
          <DxScrolling use-native="false" />
        </DxDataGrid>
        <el-pagination :page-size="pagesize" :pager-count="5" :page-sizes="[10, 20, 30, 40]"
          @size-change="handleSizeChange" layout="total, sizes, prev, pager, next, jumper" :total="total"
          @current-change="handleCurrentChange">
        </el-pagination>
      </div>
      <el-dialog title="添加" :visible.sync="updatalistinfo" width="70%" :before-close="handleToggleDialog">
        <div class="dialog_form main_form">
          <el-form ref="nodeinfoForm" :model="nodeinfoForm" :rules="rules" size="medium" label-width="180px">
            <el-form-item label="用户角色" prop="rolename">
              <el-select v-model="nodeinfoForm.rolename" placeholder="请选择用户角色" clearable :style="{ width: '100%' }">
                <el-option v-for="(item, index) in roles" :key="index" :label="item.label" :value="item.label"
                  :disabled="item.disabled"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="用户名" prop="name">
              <el-input v-model="nodeinfoForm.name" placeholder="10个字符以内，一般为用户姓名" :maxlength="10" clearable
                :style="{ width: '100%' }"></el-input>
            </el-form-item>
            <el-form-item label="用户账号" prop="account">
              <el-input v-model="nodeinfoForm.account" placeholder="请输入用户账号" clearable :style="{ width: '100%' }">
              </el-input>
            </el-form-item>
            <el-form-item label="账号密码" prop="password">
              <el-input v-model="nodeinfoForm.password" placeholder="请输入至少6位密码" clearable show-password
                :style="{ width: '100%' }"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="spassword">
              <el-input v-model="nodeinfoForm.spassword" placeholder="请输入至少6位密码" clearable :style="{ width: '100%' }">
              </el-input>
            </el-form-item>
            <el-form-item label="备注信息" prop="memo">
              <el-input v-model="nodeinfoForm.memo" type="textarea" placeholder="请输入备注信息"
                :autosize="{ minRows: 4, maxRows: 4 }" :style="{ width: '100%' }"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="updatalistinfo = false">取 消</el-button>
          <el-button type="primary" @click="handleSubmit('form')">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="修改" :visible.sync="tupdatalistinfo" width="70%" :before-close="handleToggleDialog">
        <div class="dialog_form main_form">
          <el-form ref="nodeinfoForm" :model="nodeinfoForm" :rules="trules" size="medium" label-width="180px">
            <el-form-item label="用户角色" prop="rolename">
              <el-select v-model="nodeinfoForm.rolename" placeholder="请选择用户角色" clearable :style="{ width: '100%' }">
                <el-option v-for="(item, index) in roles" :key="index" :label="item.label" :value="item.label"
                  :disabled="item.disabled"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="用户名" prop="name">
              <el-input v-model="nodeinfoForm.name" placeholder="10个字符以内，一般为用户姓名" :maxlength="10" clearable
                :style="{ width: '100%' }"></el-input>
            </el-form-item>
            <el-form-item label="用户账号" prop="account">
              <el-input v-model="nodeinfoForm.account" placeholder="请输入用户账号" clearable :style="{ width: '100%' }">
              </el-input>
            </el-form-item>
            <el-form-item label="账号密码" prop="password">
              <el-input v-model="nodeinfoForm.password" placeholder="请输入至少6位密码" clearable show-password
                :style="{ width: '100%' }"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="spassword">
              <el-input v-model="nodeinfoForm.spassword" placeholder="请输入至少6位密码" clearable :style="{ width: '100%' }">
              </el-input>
            </el-form-item>
            <el-form-item label="备注信息" prop="memo">
              <el-input v-model="nodeinfoForm.memo" type="textarea" placeholder="请输入备注信息"
                :autosize="{ minRows: 4, maxRows: 4 }" :style="{ width: '100%' }"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="tupdatalistinfo = false">取 消</el-button>
          <el-button type="primary" @click="handleSubmit('form')">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="删除" :visible.sync="deletistinfomsg" width="30%" :before-close="handleToggleDialog">
        <div class="dialog_form main_form">
          <span>是否删除{{ name }}的数据记录？</span>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deletistinfomsg = false">取 消</el-button>
          <el-button type="primary" @click="Submitdeletistinfo('form')">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import systemUser from "./systemUser";
export default systemUser;
</script>
<style scoped>
@import url("./systemUser.css");
@import url("../../assets/css/houtaicommon.css");
</style>



