import $ from 'jquery';
// import "@/libs/ztree/js/jquery.ztree.core.js"
import "ztree/js/jquery.ztree.core.min.js";
import "ztree/js/jquery.ztree.excheck.min.js";
import "ztree/css/metroStyle/metroStyle.css";
import {
  searchUserInfo,
  createuser,
  deleteuser
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  props: {},
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.nodeinfoForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      datalist: '',
      total: 0,
      pagesize: 20,
      pageindex: 1,
      updatalistinfo: false,
      deletistinfomsg: false,
      tupdatalistinfo: false,
      nodeinfoForm: '',
      password: '',
      spassword: '',
      role: '',
      deletid: '',
      name: '',
      rules: {
        rolename: [{
          required: true,
          message: '请选择用户角色',
          trigger: 'change'
        }],
        name: [{
          required: true,
          message: '10个字符以内，一般为用户姓名',
          trigger: 'blur'
        }, {
          min: 1,
          max: 10,
          message: '10个字符以内',
          trigger: 'blur'
        }],
        account: [{
          required: true,
          message: '请输入用户账号',
          trigger: 'blur'
        }],
        password: [{
            required: true,
            message: '请输入至少6位密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 30,
            message: '请输入至少6位密码',
            trigger: 'blur'
          }
        ],
        spassword: [{
          required: true,
          validator: validatePass2,
          trigger: 'blur'
        }],
        memo: [],
      },
      trules: {
        rolename: [{
          required: true,
          message: '请选择用户角色',
          trigger: 'change'
        }],
        name: [{
          required: true,
          message: '10个字符以内，一般为用户姓名',
          trigger: 'blur'
        }, {
          min: 1,
          max: 10,
          message: '10个字符以内',
          trigger: 'blur'
        }],
        account: [{
          required: true,
          message: '请输入用户账号',
          trigger: 'blur'
        }],
        password: [],
        spassword: [],
        memo: [],
      },
      roles: [{
        "label": "管理员角色",
        "value": 0
      }, {
        "label": "一般用户角色",
        "value": 2
      }, {
        "label": "审计角色",
        "value": 3
      }],
    };
  },
  mounted() {
    this.getSearchDatalist()

  },
  computed: {},
  methods: {
    // 初始化数据
    getSearchDatalist() {
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
      }
      searchUserInfo(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    // 页码数改变
    handleCurrentChange(e) {
      this.pageindex = e
      var formData = {
        pageindex: e,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
    // 数据编辑时打开弹窗
    updatalist(rowInfo) {
      this.nodeinfoForm = rowInfo.data
      this.tupdatalistinfo = true
      this.role = this.nodeinfoForm.role
      console.log('this.nodeinfoForm.id :>> ', this.nodeinfoForm);
    },
    // 编辑、新增后保存信息
    handleSubmit() {
      this.$refs.nodeinfoForm.validate(valid => {
        if (valid) {
          this.updatalistinfo = false
          this.roles.map(item => {
            if (item.label == this.nodeinfoForm.rolename) {
              this.role = item.value
            }
          })
          let formData = new FormData();
          formData = {
            rolename: this.nodeinfoForm.rolename,
            username: this.nodeinfoForm.name,
            account: this.nodeinfoForm.account,
            password: this.nodeinfoForm.password,
            memo: this.nodeinfoForm.memo,
            role: this.role,
            // id: "node-search",
          }
          createuser(formData, res => {});
          this.$nextTick(() => {
            this.getSearchDatalist()
          })
        } else {
          this.updatalistinfo = true
        }
      });

    },
    handleToggleDialog() {
      this.updatalistinfo = false;
    },
    //添加时重置信息
    reset() {
      this.nodeinfoForm = {
        rolename: "",
        name: "",
        account: "",
        password: "",
        spassword: "",
        memo: "",
      };
    },
    //添加
    openDialog() {
      this.reset();
      this.nodeinfoForm.typename = this.title
      this.$nextTick(() => {
        this.$refs["nodeinfoForm"].clearValidate();
      });
      this.updatalistinfo = true;
    },
    // 删除
    deletistinfo(rowInfo) {
      this.deletistinfomsg = true
      this.name = rowInfo.data.name
      this.deletid = rowInfo.data.id
      //   console.log('this.deletid :>> ', rowInfo);
    },
    // 确认删除
    Submitdeletistinfo() {
      this.deletistinfomsg = false
      var formData = {
        tablename: "t_data_user",
        id: this.deletid
      }
      deleteuser(formData, res => {});
      this.$nextTick(() => {
        this.getSearchDatalist()
      })
      this.getSearchDatalist()

    }
  },
};



